@import url(https://fonts.googleapis.com/css2?family=Grand+Hotel&family=Kanit:wght@100;200;300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Grand+Hotel&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F7F7F7;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  max-width: 1250px;
  margin: 0 auto;
}


.navigation{padding:0.5rem;width:100%;height:2.5rem;background:white;box-shadow:3px 6px 7px rgba(168,168,168,0.281);position:fixed;top:0;left:0;z-index:4}.navigation .nav-container{max-width:1250px;margin:0 auto;height:100%}.navigation .logo{color:#FCB8A7;font-family:'Grand Hotel', cursive;font-size:2rem;float:left;cursor:pointer}.navigation .links{line-height:2.5rem;float:right;width:auto}.navigation .links a{color:#7e7e7e;text-decoration:none;font-size:1.1rem;font-weight:bold;margin:0 1rem 0 0;transition:all 0.3s ease-in-out}.navigation .links a:hover{color:#151515}

.container{margin-top:4rem}.container div{width:100%;height:auto;font-family:"Kanit", sans-serif}.container .title{text-align:center;font-size:2.5rem;color:#4e4e4e;font-weight:400}.container .title p{text-align:center;font-size:1.2rem;font-weight:300}.container .upload-btn{margin:0 auto;height:2rem;width:2rem;border:1px solid #FEAEA5;color:#FEAEA5;border-radius:50%;font-size:1.5rem;text-align:center;padding:0;cursor:pointer;transition:all 0.3s ease-in-out;margin-bottom:1rem}.container .upload-btn::-webkit-file-upload-button{visibility:hidden}.container .upload-btn::before{margin:0 0.55rem;content:"+"}.container .upload-btn:hover{background-color:#FEAEA5;color:white}.container .upload-btn:focus{outline:none}.container .error{color:#F79384}.pictures{height:auto;width:100%;display:grid;grid-template-columns:1fr 1fr 1fr;grid-auto-rows:25vw;grid-gap:1rem;margin-bottom:1rem}@media (max-width: 1250px){.pictures{grid-auto-rows:30vw}}.pictures .picture{width:100%;opacity:0.8;overflow:hidden;border-radius:10px;transition:all 0.3s ease-in-out}.pictures .picture:hover{opacity:1}.pictures .picture img{min-width:100%;min-height:100%;max-width:150%}.progress-bar{text-align:left;padding-left:1rem;height:3px;background-color:#F5928D;font-size:1rem;color:white;transition:all 0.5s ease-in-out;border-radius:50px;margin-bottom:1rem}.spacer{height:3px;margin-bottom:6.76rem}.modal-backdrop{position:fixed;top:0;left:0;width:100%;height:100vh;background-color:rgba(0,0,0,0.5)}.modal-backdrop img{display:block;max-width:60%;max-height:80%;margin:6.5rem auto;box-shadow:3px 5px 7px rgba(0,0,0,0.5);border-radius:7px}

